function resetTranscripts(){
  final_transcript = "";
  interim_transcript = "";
}

// vue app
export default Vue.extend(
  {
    name: 'Home',
    created(){
      vueApp = this;
    },
    data() {
      return {
        isChatting: false,
        CHAT_STATE: CHAT_STATE,
        chatState: CHAT_STATE.IDLE,
        chatbackground: EMOTION.NONE,
        listening: false
      }
    },
    methods: {
      startRecognition(){
        this.listening = true;
        resetTranscripts();
        recognition.start();
      },
      stopRecognition(){
        this.listening = false;
        recognition.stop();
      },
      setBackgroundBasedOnEmotion(emotion: string){
        console.log(emotion);
        this.chatbackground = emotion;
      }
    }
  }
);


